:root{
    --accent: null
}

.FixedLayout__in {
    background-color: #007151;
}
.PanelHeader__in {
    background: url(https://zoomagasin.ru/images/im-top-logo-mobile.png) no-repeat bottom;
}

.Header__in, .Group, .Separator, .Panel .Panel__in, .Panel::after, .Tabs__in, .TabsItem__in, .Tappable, .Card    {
    background-color: #007151;
}

.RichCell, .Tappable--active {
    background-color: #007160;
}

.Text {
    color: #dcdec3;
}

.Button__in, .Button--sz-m,.Link {
    color: #dad0c2;
}
.Button--lvl-outline{
    border: 1px solid #dad0c2
}
.Tabs--default .TabsItem--selected {
    color: #a5eaa5;
}

.Tappable .TabsItem__in {
    border: 1px solid #FFF;
    padding: 5px;
    border-radius: 10px;
}

.cellCart, .Tappable .Tappable--ios .Cell__in .Tappable--inactive {
    background-color: #127951  !important;
}